import StocksList from '../components/StocksList';
import React, { useState, useEffect } from 'react';

//Get the list of companies
const StocksListPage = () => {

const [listTypeValue, listTypeInputProps] = useRadioButtons("listType");

const [items, setItems] = useState([]);

    useEffect(() => {
        fetch(`/api/Stocks-List/${listTypeValue}`)
            .then(response => response.json())
            .then(json => setItems(json))
    },[listTypeValue]);

    //Select a type of filter to output the list of companies
    return (
        <>
        <p></p>
        <div>
            <form>
                <fieldset>
                    <input
                        value="My favorites"
                        checked={listTypeValue === "My favorites"}
                        {...listTypeInputProps}
                    /> My favorites
                    
                    <input
                        value="Top companies" 
                        checked={listTypeValue === "Top companies"}
                        {...listTypeInputProps}
                    /> Top companies
                    
                    <input
                        value="Above average"
                        checked={listTypeValue === "Above average"}
                        {...listTypeInputProps}
                    /> Above average
                    
                    <input
                        value="Full list"
                        checked={listTypeValue === "Full list"}
                        {...listTypeInputProps}
                    /> Full list
                </fieldset>
            </form>
        </div>
            
        <StocksList stocks={items} />
        </>
    );
}

function useRadioButtons(name) {
    const [value, setState] = useState("Top companies");
  
    const handleChange = e => {
      setState(e.target.value);
      console.log(e.target.value)
    };
  
    const inputProps = {
      name,
      type: "radio",
      onChange: handleChange
    };
  
    return [value, inputProps];
  }


export default StocksListPage;