import React, { useState, useEffect } from 'react';
import CommentsList from '../components/CommentsList';
import FavoriteSection from '../components/FavoriteSection';
import AddNoteForm from '../components/AddNoteForm';
import NotFoundPage from './NotFoundPage';

//Internal company page
const StockPage = ({ match }) => {
    
    const symbol = match.params.symbol;

    //Initialize state for stock info
    const [stockInfo, setStockInfo] = useState({ favorite: -1, myNotes: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/stock/${symbol.toUpperCase()}`); 
            const body = await result.json();
            //console.log(body);
            setStockInfo(body);
        }
        fetchData();
    }, [symbol]);

    //When symbol does not exist in DB, return 404
    if (!stockInfo) return <NotFoundPage/>

    //Test if the company is one of my favorites
    var isItFavorite = '';
    if(stockInfo.favorite === 1) {
        isItFavorite = 'â˜… My Favorite';
    }       
         
    //Output the data
    return (
        <>
        {<h1>{stockInfo.Name} ({stockInfo.Symbol})</h1>}
        <FavoriteSection stockSymbol={symbol} favorite={isItFavorite} setStockInfo={setStockInfo} />
        <p>Points: {stockInfo.points}/16</p>
        <ul>
              <li>PE Ratio: {stockInfo.PERatio}</li>
              <li>PEG Ratio: {stockInfo.PEGRatio}</li>
              <li>Dividend Per Share: {stockInfo.DividendPerShare}</li>
              <li>Dividend Yield: {stockInfo.DividendYield}</li>
              <li>EPS: {stockInfo.EPS}</li>
              <li>Revenue Per Share (TTM): {stockInfo.RevenuePerShareTTM}</li>
              <li>ProfitMargin: {(stockInfo.ProfitMargin * 100).toFixed(2)}%</li>
              <li>Quarterly Earnings Growth (YOY): {stockInfo.QuarterlyEarningsGrowthYOY}</li>
              <li>Quarterly Revenue Growth (YOY): {stockInfo.QuarterlyRevenueGrowthYOY}</li>
              <li>Analyst Target Price: {stockInfo.AnalystTargetPrice}</li>
              <li>Price To Book Ratio: {stockInfo.PriceToBookRatio}</li>
            </ul>  
        {<p>Sector: {stockInfo.Sector}<br></br>Industry: {stockInfo.Industry}<br></br>{stockInfo.Description}</p>}
        <CommentsList comments={stockInfo.myNotes} />
        <AddNoteForm stockSymbol={symbol} setStockInfo={setStockInfo} />
        </>
    );
}

export default StockPage;    