//The component allows to set a company as my favorite or not 
import React from 'react';

const FavoriteSection = ({ stockSymbol, favorite, setStockInfo }) => {
    const setAsDefault = async () => {
        const result = await fetch(`/api/stock/${stockSymbol.toUpperCase()}/favorite`, {
            method: 'post',
        });
        const body = await result.json();
        setStockInfo(body);
    }
    return (
        <div id="upvotes-section">
            <button onClick={() => setAsDefault()}>Favorite</button>
            {<p>{favorite}</p>}  
        </div>
    );
}

export default FavoriteSection;