import React from 'react';
import axios from 'axios';


//Get data by stock symbol from external API, display it on the Home page
class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
        name: "",
        symbol: "",
        PERatio: "",
        PEGRatio: "",
        DividendPerShare: "",
        DividendYield: "",
        EPS: "",
        RevenuePerShareTTM: "",
        ProfitMargin: "",
        QuarterlyEarningsGrowthYOY: "",
        QuarterlyRevenueGrowthYOY: "",
        AnalystTargetPrice: "",
        PriceToBookRatio: "",
        description: "",
        test: "",
    }
  }

  //Do on submit
  mySubmitHandler = (event) => {
    event.preventDefault();
    
    //Call data fetch if a symbol was inserted
    if (this.state.symbol) {
      this.setState({test: "testPassed"});
      this.fetchData(this.state.symbol);      
    } else {
      alert("Please type a company ticker first")
    }    
    this.setState({symbol: ""});
  }

  //Get data from the URL and call function to update DB
  fetchData(symbolIn) {
    axios.get("https://www.alphavantage.co/query?function=OVERVIEW&symbol=" + symbolIn + "&apikey=02NIDMNOYiucy81v")
    .then(response => {
      console.log(response.data);
      console.log("Data received");
      this.setState({ 
        name: response.data.Name, 
        symbol: response.data.Symbol,
        PERatio: response.data.PERatio,
        PEGRatio: response.data.PEGRatio,
        DividendPerShare: response.data.DividendPerShare,
        DividendYield: response.data.DividendYield,
        EPS: response.data.EPS,
        RevenuePerShareTTM: response.data.RevenuePerShareTTM,
        ProfitMargin: response.data.ProfitMargin,
        QuarterlyEarningsGrowthYOY: response.data.QuarterlyEarningsGrowthYOY,
        QuarterlyRevenueGrowthYOY: response.data.QuarterlyRevenueGrowthYOY,
        AnalystTargetPrice: response.data.AnalystTargetPrice,
        PriceToBookRatio: response.data.PriceToBookRatio,
        description: response.data.Description });

        this.addCompany(response.data.Symbol, response.data);
      })
      
    .catch(error => {
      console.log(error);
    });    
  }

  //Insert new company into DB or update the existed
  addCompany = (stockSymbol, values) => {
    axios
      .post(`/api/stock/${stockSymbol.toUpperCase()}/add-stock`, values)
      .then(res => {
        console.log(res);
        console.log("Inserted into DB");
      })
      .catch(error => {
        console.log(error);
      });
  };
  
  render() {
    const { 
      name, 
      PERatio,
      PEGRatio,
      DividendPerShare,
      DividendYield,
      EPS,
      RevenuePerShareTTM,
      ProfitMargin,
      QuarterlyEarningsGrowthYOY,
      QuarterlyRevenueGrowthYOY,
      AnalystTargetPrice,
      PriceToBookRatio,
      description } = this.state;

    //Show data when submited
    let info = '';
    if (this.state.test) {
      info = 
      <div>
        <p><small>The data for the company with ticker ({this.state.symbol}) will be inserted into the database</small></p>
      <div>
        <p>{name}</p>  
        <ul>
          <li>PE Ratio: {PERatio}</li>
          <li>PEG Ratio: {PEGRatio}</li>
          <li>Dividend Per Share: {DividendPerShare}</li>
          <li>Dividend Yield: {DividendYield}</li>
          <li>EPS: {EPS}</li>
          <li>Revenue Per Share (TTM): {RevenuePerShareTTM}</li>
          <li>ProfitMargin: {(ProfitMargin * 100).toFixed(2)}%</li>
          <li>Quarterly Earnings Growth (YOY): {QuarterlyEarningsGrowthYOY}</li>
          <li>Quarterly Revenue Growth (YOY): {QuarterlyRevenueGrowthYOY}</li>
          <li>Analyst Target Price: {AnalystTargetPrice}</li>
          <li>Price To Book Ratio: {PriceToBookRatio}</li>
        </ul>        
        <p>{description}</p>         
      </div>
    </div>;
    } else {
      info = '';
    }
    
    //Symbol insert with submit button
    return (
      <form onSubmit={this.mySubmitHandler}>
       
        <h1 align="center">US Stocks Investment Advisor Tool</h1>  

        <div id="add-comment-form">
          <label>
              <textarea rows="1" cols="50" placeholder="Company symbol" onChange={(event) => this.setState({symbol: event.target.value})}  />
          </label>
              <button type='submit'>Get data</button>
          </div>
        {info}
     </form>
    );
  }
}

export default HomePage;

