import React from 'react';

const AboutPage = () => (
    <>
    <p>
        <i>This WEB application, by being connected to external API data source, 
        allows to extract US companies fundamentals, save data to a database, 
        leave personal notes, mark favorite companies, 
        and get a list of equities, based on predefined rules.</i>
    </p>
    <h3>Home page</h3>
    <p>
        Get any US company fundamental data by stock symbol from the external data source.
        Every request updates the database accordingly. 
    </p>
    <h3>Companies</h3>
    <p>
    Any company that meets one of the 16 predifined conditions receives one point. 
    All companies in the list rated by the number of points they get.
    Users favorite companies have advantage among others at the same level.
    </p>
    Rules:
    <ul> 
        <li>Market Capitalization above - 2bln</li>
        <li>PE Ratio - between 8 and 40</li>
        <li>PEG Ratio - between 0 and 4</li>
        <li>EV To Revenue - between 0 and 4</li>
        <li>EPS - above 0</li>
        <li>Quarterly Earnings Growth (YOY) - above 0</li>
        <li>Quarterly Revenue Growth (YOY) - above 0</li>
        <li>Profit Margin - equal or above 10%</li>
        <li>ReturnOnAssets (TTM) - equal or above 4%</li>
        <li>ReturnOnEquity (TTM) - equal or above 4%</li>
        <li>Price To Sales Ratio (TTM) - between 0 and 3</li>
        <li>Price To Book Ratio - between 0 and 4</li>
        <li>Beta - between -2 and 2</li>
        <li>Percent Insiders - between 1 and 70</li>
        <li>Percent Institutions - above 5</li>
        <li>PayoutRatio - below 50%</li>
    </ul>
    <p>Choose one from the four options available to display the list of you favorite companies, 
        or companies with the maximum number of points, average companies, or the whole list from the database.</p>
    
    <h3>Company page</h3>
    <p>
        Get the company main data from the database.<br></br>
        The application allows to leave your personal notes and add companies into the list of favorites.
        <br></br>
        <br></br>
    </p>
    </>
);

export default AboutPage;