import React, { useState } from 'react';

//Allows to add personal notes on company pages
const AddCommentForm = ({ stockSymbol, setStockInfo }) => {
    const [commentText, setCommentText] = useState('');

    const addComment = async () => {
        const result = await fetch(`/api/stock/${stockSymbol.toUpperCase()}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ myText: commentText }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setStockInfo(body);
        setCommentText('');
    }

    return (
        <div id="add-comment-form">
            <label>
                <textarea rows="4" cols="50" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
            </label>
            <button onClick={() => addComment()}>Post a note</button>
        </div>
    );
}

export default AddCommentForm;